import {
		mapState,  
	    mapMutations  
	} from 'vuex'; 
	
export default {
	data() {
		return {
			costList: [],
			TabCur: 0,
		}
	},
	computed: {
		...mapState(['token', 'globalUserInfo'])
	},
	onLoad() {
		// uni.request({
		//     url: this.global_baseUrl + '/costInfo/',
		//     data: {
				
		//     },
		//     header: {
		// 		'Authorization': 'Token  ' + this.token,
		//     },
		//     success: (res) => {
		// 		console.log(res.data);
		// 		this.costList = res.data;
		//     }
		// });
		// setTimeout(function () {
		//     uni.hideLoading();
		// }, 1000);
	},
	onShow(){
		var that = this;
		that.checkLocalInfo();
		var that = this;
		if(that.token !== null){
			uni.request({
				url: this.global_baseUrl + '/costInfo/',
				data: {
					
				},
				header: {
					'Authorization': 'Token  ' + this.token,
				},
				success: (res) => {
					this.costList = res.data;
				}
			});
		}
		setTimeout(function () {
		    uni.hideLoading();
		}, 1000);
	},
	methods: {
		checkLocalInfo() {
			var that = this;
			if(that.token == null){
				setTimeout(() => {
					uni.showModal({
						title: '提示',
						content: '您还未登录，登录体验更多功能',
						cancelText: '先逛逛',
						confirmText: '去登录',
						success: function(res) {
							if (res.confirm) {
								uni.navigateTo({
									url: '/pages/login/login'
								});
							} else if (res.cancel) {}
						}
					});
				}, 800)
				uni.hideLoading();
			} else {
				uni.hideLoading();
			}
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
		},
	}
}
